import { Controller, Commands } from '@ali/4ever-cangjie';
import { generateKey} from '@ali/4ever-utils';


export interface ImageProperties {
  src: string;
  width?: number;
  height?: number;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  extraData?: Record<string, any>;
}

export default function insertImage(
  controller: Controller,
  maybeImages: ImageProperties | ImageProperties[],
) {
  if (controller.query('isCollapsedInBlockVoid')) {
    controller.command(Commands.insertBlock, 'paragraph');
  }

  const images = Array.isArray(maybeImages) ? maybeImages : [maybeImages];
  images.forEach((image) => {
    controller
      .command(Commands.insertInline, {
        type: 'image',
        data: {
          id: generateKey(),
          name: '',
          src: image.src,
          width: image.width,
          height: image.height,
          extraData: image.extraData,
        },
      })
      .command(Commands.moveToStartOfNextText)
      .command(Commands.insertText, ' ');
  });

  return controller.command(Commands.focus);
}
